import Api from './api.js'

const api = Api();

export default {
  isDev() {
    return process.env.NODE_ENV && process.env.NODE_ENV === 'development'
  },
  getHouseholds() {
    return api.get('/households')
      .then(response => response.data)
      .catch(err => {
        console.log(err)
      });
  },
  getUsers() {
    return api.get('/users')
      .then(response => response.data)
      .catch(err => {
        console.log(err)
      });
  },
  getShoppingLists() {
    return api.get('/shoppinglists')
      .then(response => response.data)
      .catch(err => {
        console.log(err)
      });
  },
  getShoppingItems() {
    return api.get('/shoppingitems')
      .then(response => response.data)
      .catch(err => {
        console.log(err)
      });

  },
  getHousehold(id) {
    return this.getHouseholds()
      .then(households => {
        if (households != null) {
          return households.find(household => household.id === id);
        }
      });
  },
  getUserHouseholds(uid) {
    return this.getHouseholds()
      .then(households => {
        if (households != null && households.tenants != null) {
          return households.tenants.find(tenant => tenant != null && tenant.id === uid);
        }
      })
  },
  getUser(id) {
    return this.getUsers().then(users => {
      if (users != null) {
        return users.find(user => user.id === id);
      }
    });
  },
  getShoppingList(id) {
    return this.getShoppingLists().then(lists => {
      if (lists != null) {
        return lists.find(list => list.id === id);
      }
    });
  },

  getShoppingItem(id) {
    return this.getShoppingItems().then(si => si ? si.find(i => i.id != null && i.id === id) : null);
  },

  buyShoppingItem(id) {
    return api.post(`/shoppingitems/buy/${Number(id)}`);
  }
}



